import React, { useState, useCallback, memo, useMemo, useEffect } from 'react';
import {
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Checkbox,
  Typography,
  Chip,
  Box,
  Paper,
  Button,
  useTheme,
  useMediaQuery,
  Stack,
  Snackbar,
  Alert,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from '@mui/material';
import {
  Delete as DeleteIcon,
  Edit as EditIcon,
  Alarm as AlarmIcon,
  Timer as TimerIcon,
  PlayArrow as PlayIcon,
  Stop as StopIcon,
  Sort as SortIcon,
} from '@mui/icons-material';
import { motion, AnimatePresence } from 'framer-motion';
import { Task } from '../types/Task';
import { TaskTimer } from './TaskTimer';
import {
  listItemVariants,
  timerVariants,
  buttonHoverVariants,
} from '../animations/variants';

interface TaskListProps {
  tasks: Task[];
  onDelete: (taskId: string) => void;
  onToggleComplete: (taskId: string, completed: boolean) => void;
}

interface TaskItemProps {
  task: Task;
  isMobile: boolean;
  activeTimerId: string | null;
  onDelete: (taskId: string) => void;
  onToggleComplete: (taskId: string, completed: boolean) => void;
  onStartTimer: (taskId: string) => void;
  formatWorkTime: (minutes: number) => string;
}

const MotionPaper = motion(Paper);

// 
const dayLabels: Record<string, string> = {
  sunday: '',
  monday: '',
  tuesday: '',
  wednesday: '',
  thursday: '',
  friday: '',
  saturday: '',
};

// 
const TaskChip = memo(({ label, icon, color, variant = 'outlined', isReminder = false, isLoop = false }: any) => (
  <Chip
    label={isLoop ? `${label} ()` : label}
    icon={icon}
    color={color}
    size="small"
    variant={variant}
    sx={isReminder ? styles.reminderChip : undefined}
  />
));

// 
const styles = {
  taskTitle: {
    fontSize: '1.1rem',
    fontWeight: 500,
    color: '#1a1a1a',
    lineHeight: 1.4,
    marginBottom: '4px',
  },
  taskTitleMobile: {
    fontSize: '1.2rem',
    fontWeight: 500,
    color: '#1a1a1a',
    lineHeight: 1.4,
    marginBottom: '8px',
  },
  taskChips: {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '8px',
  },
  taskDescription: {
    wordBreak: 'break-word',
    whiteSpace: 'pre-wrap',
    lineHeight: 1.5,
    color: '#666666', // 
  },
  reminderChip: {
    backgroundColor: '#e3f2fd', // 
    color: '#1976d2',          // 
    '& .MuiChip-icon': {
      color: '#1976d2',        // 
    },
  },
  checkbox: {
    '&.MuiCheckbox-root': {
      padding: '12px',
      color: '#9e9e9e',  // 
    },
    '&.Mui-checked': {
      color: '#4caf50',  // 
    },
    '&:hover': {
      backgroundColor: 'rgba(76, 175, 80, 0.08)',  // 
    },
    '& .MuiSvgIcon-root': {
      fontSize: '1.5rem',  // 
    },
  },
  listItem: {
    borderLeft: '4px solid transparent',
    '&:hover': {
      borderLeft: '4px solid #e0e0e0',
      backgroundColor: 'rgba(0, 0, 0, 0.02)',
    },
  },
  completedItem: {
    backgroundColor: '#f5f5f5',
    opacity: 0.7,
    transition: 'all 0.3s ease',
  },
};

// 
const fadeOutVariants = {
  initial: { opacity: 1, height: 'auto' },
  exit: {
    opacity: 0,
    height: 0,
    marginBottom: 0,
    transition: {
      opacity: { duration: 0.2 },
      height: { duration: 0.3, delay: 0.1 },
      marginBottom: { duration: 0.3, delay: 0.1 },
    },
  },
};

// 
const TaskItem = memo(({
  task,
  isMobile,
  activeTimerId,
  onDelete,
  onToggleComplete,
  onStartTimer,
  formatWorkTime,
}: TaskItemProps) => {
  // 
  const selectedDays = useMemo(() => {
    if (!task.reminderDays) return '';
    return Object.entries(task.reminderDays)
      .filter(([_, isSelected]) => isSelected)
      .map(([day]) => dayLabels[day])
      .join('');
  }, [task.reminderDays]);

  // 
  const chips = useMemo(() => ({
    priority: task.priority === 'high',
    category: !!task.category,
    reminder: !!task.reminderTime,
    workTime: (task.workTime ?? 0) > 0,
  }), [task.priority, task.category, task.reminderTime, task.workTime]);

  // 
  const handleCheckboxChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    onToggleComplete(task.id, e.target.checked);
  }, [task.id, onToggleComplete]);

  return (
    <motion.div
      variants={fadeOutVariants}
      initial="initial"
      exit="exit"
      layout
    >
      <MotionPaper
        elevation={1}
        variants={listItemVariants}
        initial="hidden"
        animate="visible"
        sx={{
          mb: 1,
          backgroundColor: task.completed ? '#f5f5f5' : 'white',
          overflow: 'hidden',
          opacity: task.completed ? 0.7 : 1,
          transition: 'opacity 0.3s ease, background-color 0.3s ease',
        }}
      >
        <ListItem
          sx={{
            ...styles.listItem,
            ...(task.completed ? styles.completedItem : {}),
          }}
        >
          <Checkbox
            checked={task.completed}
            onChange={handleCheckboxChange}
            sx={styles.checkbox}
            color="primary"
          />
          <Box sx={{ flexGrow: 1, minWidth: 0 }}>
            <Box sx={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row', gap: 1 }}>
              <Typography
                variant={isMobile ? 'h6' : 'subtitle1'}
                component="div"
                sx={{
                  ...(isMobile ? styles.taskTitleMobile : styles.taskTitle),
                  textDecoration: task.completed ? 'line-through' : 'none',
                  wordBreak: 'break-word',
                }}
              >
                {task.title}
              </Typography>
              <Stack
                direction={isMobile ? 'column' : 'row'}
                spacing={1}
                sx={{ 
                  mt: isMobile ? 1 : 0,
                  ...styles.taskChips
                }}
              >
                {chips.priority && (
                  <TaskChip
                    label=""
                    color="error"
                  />
                )}
                {chips.category && (
                  <TaskChip
                    label={task.category}
                    color="primary"
                  />
                )}
                {chips.reminder && (
                  <TaskChip
                    icon={<AlarmIcon />}
                    label={`${task.reminderTime}${selectedDays ? ` (${selectedDays})` : ''}`}
                    color="info"
                    isReminder={true}
                    isLoop={task.isLoopReminder}
                  />
                )}
                {chips.workTime && (
                  <TaskChip
                    icon={<TimerIcon />}
                    label={formatWorkTime(task.workTime ?? 0)}
                    color="success"
                  />
                )}
              </Stack>
            </Box>
            <Box sx={{ mt: 1 }}>
              <Typography
                variant="body2"
                component="div"
                sx={styles.taskDescription}
              >
                {task.description}
              </Typography>
              {(task.workTime ?? 0) > 0 && (
                <motion.div
                  variants={buttonHoverVariants}
                  initial="initial"
                  whileHover="hover"
                  whileTap="tap"
                >
                  <Button
                    startIcon={<PlayIcon />}
                    size="small"
                    onClick={() => onStartTimer(task.id)}
                    disabled={!!activeTimerId || task.completed}
                    sx={{ mt: 1 }}
                  >
                    
                  </Button>
                </motion.div>
              )}
            </Box>
          </Box>
          <ListItemSecondaryAction sx={{ display: 'flex', gap: 1 }}>
            <motion.div
              variants={buttonHoverVariants}
              initial="initial"
              whileHover="hover"
              whileTap="tap"
            >
              <IconButton
                edge="end"
                aria-label="delete"
                onClick={() => onDelete(task.id)}
                sx={{
                  color: '#f44336',
                  '&:hover': {
                    backgroundColor: 'rgba(244, 67, 54, 0.08)',
                  },
                }}
              >
                <DeleteIcon />
              </IconButton>
            </motion.div>
          </ListItemSecondaryAction>
        </ListItem>
      </MotionPaper>
    </motion.div>
  );
});

// 
interface Notification {
  id: string;
  message: string;
  type: 'success' | 'info' | 'warning' | 'error';
}

// 
interface ActiveTimer {
  taskId: string;
  startTime: Date;
}

// 
type SortType = 'created' | 'title' | 'priority' | 'reminder';

export const TaskList = memo(({
  tasks,
  onDelete,
  onToggleComplete,
}: TaskListProps) => {
  const [activeTimers, setActiveTimers] = useState<ActiveTimer[]>([]);
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [sortType, setSortType] = useState<SortType>('created');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const formatWorkTime = useCallback((minutes: number): string => {
    if (minutes < 60) {
      return `${minutes}`;
    }
    const hours = Math.floor(minutes / 60);
    const remainingMinutes = minutes % 60;
    return remainingMinutes > 0 ? `${hours}${remainingMinutes}` : `${hours}`;
  }, []);

  const handleStartTimer = useCallback((taskId: string) => {
    setActiveTimers(prev => [...prev, { taskId, startTime: new Date() }]);
  }, []);

  const handleStopTimer = useCallback((taskId: string) => {
    setActiveTimers(prev => prev.filter(timer => timer.taskId !== taskId));
  }, []);

  // 
  const activeTasks = useMemo(() => {
    const uncompletedTasks = tasks.filter(task => !task.completed);
    
    switch (sortType) {
      case 'title':
        return uncompletedTasks.sort((a, b) => a.title.localeCompare(b.title));
      case 'priority':
        return uncompletedTasks.sort((a, b) => {
          if (a.priority === 'high' && b.priority !== 'high') return -1;
          if (a.priority !== 'high' && b.priority === 'high') return 1;
          return a.title.localeCompare(b.title);
        });
      case 'reminder':
        return uncompletedTasks.sort((a, b) => {
          if (!a.reminderTime && !b.reminderTime) return 0;
          if (!a.reminderTime) return 1;
          if (!b.reminderTime) return -1;
          return a.reminderTime.localeCompare(b.reminderTime);
        });
      case 'created':
      default:
        return uncompletedTasks.sort((a, b) => b.id.localeCompare(a.id));
    }
  }, [tasks, sortType]);

  // 
  const handleSortChange = useCallback((event: React.ChangeEvent<{ value: unknown }>) => {
    setSortType(event.target.value as SortType);
  }, []);

  // 
  const showNotification = useCallback((task: Task, isReminder: boolean = false) => {
    try {
      const audio = new Audio('/notification.mp3');
      audio.volume = 0.5;
      audio.play().catch(error => {
        console.warn(':', error);
      });

      if ('Notification' in window && Notification.permission === 'granted') {
        const notification = new Notification(
          isReminder ? '' : '',
          {
            body: isReminder 
              ? `${task.title}`
              : `${task.title}`,
            icon: '/favicon.ico',
            requireInteraction: true,
            silent: true
          }
        );

        notification.onclick = () => {
          window.focus();
          notification.close();
        };

        setTimeout(() => notification.close(), 10000);
      }

      setNotifications(prev => [
        ...prev,
        {
          id: Date.now().toString(),
          message: isReminder 
            ? `${task.title}`
            : `${task.title}`,
          type: 'info',
        },
      ]);
    } catch (error) {
      console.error(':', error);
    }
  }, []);

  // 
  const checkTasks = useCallback(() => {
    const now = new Date();
    const currentHour = now.getHours();
    const currentMinute = now.getMinutes();
    const currentDay = now.getDay();
    const dayNames = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'] as const;
    const currentDayName = dayNames[currentDay];
    
    console.log(':', {
      currentTime: `${currentHour}:${currentMinute}`,
      currentDay: currentDayName
    });

    // 
    const activeTasks = tasks.filter(task => 
      !task.completed && 
      task.reminderTime && 
      !localStorage.getItem(`notification_${task.id}_${now.toDateString()}`)
    );

    console.log(':', activeTasks.length);

    // 
    const tasksToCheck = activeTasks.filter(task => {
      console.log(':', {
        title: task.title,
        reminderTime: task.reminderTime,
        reminderDays: task.reminderDays,
        completed: task.completed
      });
      
      // 
      const [hours, minutes] = task.reminderTime.split(':').map(Number);
      const isTimeMatch = hours === currentHour && minutes === currentMinute;
      
      if (!isTimeMatch) {
        console.log(' : ');
        return false;
      }

      // 
      // reminderDays
      const reminderDaysStatus = {
        isNull: task.reminderDays === null,
        isUndefined: task.reminderDays === undefined,
        isEmpty: task.reminderDays && Object.keys(task.reminderDays).length === 0,
        hasAnyTrueDay: task.reminderDays && 
          Object.values(task.reminderDays).some(value => value === true),
        currentDayValue: task.reminderDays?.[currentDayName],
        allDayValues: task.reminderDays ? Object.entries(task.reminderDays)
          .map(([day, value]) => `${day}: ${value}`).join(', ') : ''
      };

      console.log('reminderDays:', reminderDaysStatus);
      
      // 
      const isNoReminderDays = !reminderDaysStatus.hasAnyTrueDay;
      
      const shouldNotify = isNoReminderDays || 
        (task.reminderDays && task.reminderDays[currentDayName] === true);

      console.log(':', {
        title: task.title,
        shouldNotify,
        isNoReminderDays,
        reminderDays: task.reminderDays,
        reason: isNoReminderDays ? '' : 
          shouldNotify ? `${currentDayName}` : ''
      });

      return shouldNotify;
    });

    // 
    if (tasksToCheck.length > 0) {
      console.log(':', tasksToCheck.map(task => ({
        title: task.title,
        reminderTime: task.reminderTime,
        reminderDays: task.reminderDays || ''
      })));
    } else {
      console.log('');
    }

    tasksToCheck.forEach(task => {
      console.log(`: ${task.title}`);
      showNotification(task, true);
      const notificationKey = `notification_${task.id}_${now.toDateString()}`;
      localStorage.setItem(notificationKey, 'true');
    });
  }, [tasks, showNotification]);

  // 
  useEffect(() => {
    const now = new Date();
    const nextMinute = new Date(now);
    nextMinute.setMinutes(now.getMinutes() + 1, 0, 0);
    const timeUntilNextCheck = nextMinute.getTime() - now.getTime();

    // 
    checkTasks();

    // 
    const timeoutId = setTimeout(() => {
      checkTasks();
      // 1
      const intervalId = setInterval(checkTasks, 60000);
      return () => clearInterval(intervalId);
    }, timeUntilNextCheck);

    return () => clearTimeout(timeoutId);
  }, [checkTasks]);

  // 
  const handleTimerComplete = useCallback((taskId: string) => {
    const task = tasks.find(t => t.id === taskId);
    if (task) {
      showNotification(task, false);
    }
    handleStopTimer(taskId);
  }, [tasks, showNotification, handleStopTimer]);

  const handleCloseNotification = useCallback((notificationId: string) => {
    setNotifications(prev => prev.filter(n => n.id !== notificationId));
  }, []);

  // 
  const activeTimerTasks = useMemo(() => {
    return activeTimers
      .map(timer => {
        const task = tasks.find(t => t.id === timer.taskId);
        return task ? {
          ...task,
          startTime: timer.startTime,
        } : null;
      })
      .filter((task): task is NonNullable<typeof task> => task !== null);
  }, [activeTimers, tasks]);

  // 
  const handleToggleComplete = useCallback((taskId: string, completed: boolean) => {
    if (completed) {
      // 1
      setTimeout(() => {
        onDelete(taskId);
      }, 1000);
    }
    onToggleComplete(taskId, completed);
  }, [onDelete, onToggleComplete]);

  return (
    <Box>
      {/*  */}
      <Box sx={{ mb: 2 }}>
        <FormControl size="small" sx={{ minWidth: 200 }}>
          <InputLabel id="sort-select-label">
            <Stack direction="row" spacing={1} alignItems="center">
              <SortIcon fontSize="small" />
              <span></span>
            </Stack>
          </InputLabel>
          <Select
            labelId="sort-select-label"
            value={sortType}
            label={
              <Stack direction="row" spacing={1} alignItems="center">
                <SortIcon fontSize="small" />
                <span></span>
              </Stack>
            }
            onChange={handleSortChange}
          >
            <MenuItem value="created"></MenuItem>
            <MenuItem value="title"></MenuItem>
            <MenuItem value="priority"></MenuItem>
            <MenuItem value="reminder"></MenuItem>
          </Select>
        </FormControl>
      </Box>

      {notifications.map(notification => (
        <Snackbar
          key={notification.id}
          open={true}
          autoHideDuration={10000}
          onClose={() => handleCloseNotification(notification.id)}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          sx={{
            '& .MuiSnackbarContent-root': {
              minWidth: '300px',
            },
          }}
        >
          <Alert
            onClose={() => handleCloseNotification(notification.id)}
            severity={notification.type}
            variant="filled"
            elevation={6}
            sx={{
              width: '100%',
              '& .MuiAlert-message': {
                fontSize: '1rem',
              },
            }}
          >
            {notification.message}
          </Alert>
        </Snackbar>
      ))}

      <Box sx={{ mb: 2 }}>
        {activeTimerTasks.map(task => (
          <TaskTimer
            key={task.id}
            taskId={task.id}
            taskTitle={task.title}
            totalMinutes={task.workTime ?? 0}
            onComplete={() => handleTimerComplete(task.id)}
          />
        ))}
      </Box>

      <List>
        <AnimatePresence mode="sync" initial={false}>
          {activeTasks.map((task) => (
            <TaskItem
              key={task.id}
              task={task}
              isMobile={isMobile}
              activeTimerId={activeTimers.some(timer => timer.taskId === task.id) ? task.id : null}
              onDelete={onDelete}
              onToggleComplete={handleToggleComplete}
              onStartTimer={handleStartTimer}
              formatWorkTime={formatWorkTime}
            />
          ))}
        </AnimatePresence>
      </List>
    </Box>
  );
}); 